import { ReactNode } from 'react';

interface ButtonProps {
  children: ReactNode;
  variant?: 'primary' | 'secondary';
  icon?: string;
  className?: string;
  onClick?: () => void;
  type?: 'button' | 'submit' | 'reset';
}

export default function Button({ 
  children, 
  variant = 'primary', 
  icon, 
  className = '',
  ...props 
}: ButtonProps) {
  const baseClasses = 'btn inline-flex items-center gap-2 px-5 py-[0.6rem] rounded-lg font-semibold text-sm cursor-pointer border-none transition-[var(--transition)]';
  const variantClasses = {
    primary: 'btn-primary bg-[var(--primary)] text-white shadow-[0_2px_4px_rgba(67,97,238,0.3)] hover:bg-[var(--primary-hover)] hover:-translate-y-[2px] hover:shadow-[0_4px_6px_rgba(67,97,238,0.3)]',
    secondary: 'bg-[var(--secondary)] text-white shadow-md hover:bg-[#05c295] hover:-translate-y-[2px] hover:shadow-lg'
  };

  return (
    <button 
      className={`${baseClasses} ${variantClasses[variant]} ${className}`} 
      {...props}
    >
      {icon && <i className={`fas fa-${icon}`}></i>}
      {children}
    </button>
  );
}